body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Nav ul {
  display: -webkit-flex;
  display: flex;
  padding: 0;
  margin: 0;
}

.Nav ul li {
  list-style-type: none;
  padding: 10px;
}

/* SEARCHBOX COMP */
.SearchBox {
  padding: 0;
  margin: 0 auto;
  position: relative;
  display: -webkit-flex;
  display: flex;
}

.SearchBox input {
  border: 1.5px solid var(--main-color);
  border-right: none;
  padding: 10px 20px;
  -webkit-flex: 1 1;
          flex: 1 1;

}

.SearchBox input[placeholder] {
  font-size: inherit;
}

.SearchBox input:focus, input:focus input[placeholder] {
  color: var(--main-color);
  font-weight: 600;
}

.SearchBox input:-internal-autofill-previewed, .SearchBox input:-internal-autofill-selected {
  background-color: rgba(159, 58, 58, .8);
  color: #fff;
  font-weight: 600;
}

.search-btn {
  /* position: absolute; */
  /* top: 50%;
  right: 0; */
  /* transform: translateY(-50%); */
  background: none;
  border: none;
  color: var(--main-color);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 0;
}

.SearchBox .search-btn:hover {
  /* border: 1px solid rgba(7, 111, 170,0.5); */
  box-shadow: 0 5px 10px -5px rgba(0,0,0,0.2);
  cursor: pointer;
}

.SearchBox .search-btn:hover #lupita-svg {
  cursor: pointer;
  fill: #fff;
  opacity: 1;
}

.SearchBox #lupita-svg {
  padding: 10px;
  height: 50px;
  width: 80px;
  background-color: var(--main-color);
  fill: #ddd;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
  pointer-events: none;
  opacity: .9;
}

@media  (max-width: 500px) {
  .SearchBox input {
    padding: 10px;
  }
  .SearchBox #lupita-svg {
    height: 48px;
    width: auto;
  }
}



.CardsList {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  margin: 0 -100px;
  grid-gap: 20px;
}


.Card {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 30px;
  border: 2px solid var(--main-color);
  border-left: 1em solid var(--main-color);
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  -webkit-transition: all 300ms ease;
  transition: all 300ms ease;
  box-shadow:
  3px 3px 0px 0px rgba(255, 255, 255, 1),
  5px 5px 0px 0px rgba(159, 58, 58, .7),
  7px 7px 0px 0px rgba(255, 255, 255, 1),
  9px 9px 0px 0px rgba(159, 58, 58, .5)
  ;

}

.Card:hover .book-img{
  /* background-color: rgba(159, 58, 58, .1); */
  -webkit-transform: translate(8px, -8px) rotate(-3deg);
          transform: translate(8px, -8px) rotate(-3deg);
  box-shadow: 2px 2px 5px 0 rgba(159, 58, 58, .7);
}


