.book-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
  height: 100%;
}

.book-title {
  position: relative;
  margin: 10px;
  padding: 10px;
  text-align: center;
  color: var(--main-color);
}

.book-title::after {
  content: "";
  position: absolute;
  width: 50%;
  bottom: 0;
  left: 0;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
  border-bottom: 4px solid rgba(159, 58, 58, .8);
}

.book-info {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.book-img {
  padding: 10px;
